body {
  background-color: #393E46;
  text-align: center;
}

h1 {
  margin: 30px;
  font-family: 'Lobster', cursive;
  text-shadow: 5px 0 #232931;
  font-size: 8rem;
  color: #acb2bd;
}

p {
  font-size: 2rem;
  color: #acb2bd;
  font-family: 'Indie Flower', cursive;
}

img {
  width: 80%;
}

footer {
  margin-top: 5%;
  color: #EEEEEE;
  text-align: center;
  font-family: 'Indie Flower', cursive;
}


.container {
  width: 70%;
  margin: auto;
  text-align: center;
}

.dice {
  text-align: center;
  display: inline-block;
}

.dice p{
  
  color: #393E46;
}

.diceDisplay{
  padding: 1% 15%;
  background-color: #acb2bd;
}

.sliderDisplay{
  width: 500px;
  margin: 0 auto;
}

